.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 70px;
    box-shadow: 0px 10px 63px rgba(0, 0, 0, 0.07);
    background-color: var(--white);
}

.logo {
    display: flex;
    align-items: center;
    font-size: 24px;
    font-weight: 300;
}

.logo, .logo:visited {
    color: var(--red);
    transition: color var(--state-delay);
}

.logo:hover {
    color: var(--red-dark);
}

.logo > svg {
    margin-right: 14px;
}

.tools {
    display: flex;
    align-items: center;
}

.headerLink {
    display: flex;
    align-items: center;
    font-size: 16px;
    font-family: SFL,sans-serif;
    fill: var(--red);
    transition: color var(--state-delay), fill var(--state-delay);
}

.headerLink:not(:last-child){
    margin-right: 15px;
}

.headerLink > svg {
    margin-right: 9px;
}

.headerLink, .headerLink:visited {
    color: var(--red);
}

.headerLink:hover {
    fill: var(--red-dark);
    color: var(--red-dark);
}